<template>
  <div v-if="categories" class="categoriess">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-button type="primary" @click="addCatDialog">添加分类</el-button>
      <el-table
        :data="categories.result"
        border
        style="width: 100%"
        :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
        row-key="cat_id"
      >
        <el-table-column label="#" width="100"> </el-table-column>
        <el-table-column prop="cat_name" label="分类名称" width="180">
        </el-table-column>
        <el-table-column prop="cat_deleted" label="是否有效" width="180">
          <template v-slot="{ row }">
            <i
              :class="row.cat_deleted ? 'el-icon-close' : 'el-icon-success'"
            ></i>
          </template>
        </el-table-column>
        <el-table-column prop="cat_level" label="排序">
          <template v-slot="{ row }">
            <el-tag
              size="mini "
              effect="dark"
              :type="
                row.cat_level === 0
                  ? ''
                  : row.cat_level === 1
                  ? 'success'
                  : 'warning'
              "
              >{{
                row.cat_level === 0
                  ? "一级"
                  : row.cat_level === 1
                  ? "二级"
                  : "三级"
              }}</el-tag
            >
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180">
          <template v-slot="{ row }">
            <el-button
              size="small"
              type="primary"
              icon="el-icon-edit"
              @click="editCatDialog(row)"
              >编辑</el-button
            >
            <el-button
              @click="delCat(row.cat_id)"
              size="small"
              type="danger"
              icon="el-icon-delete"
              >删除</el-button
            ></template
          >
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="sizeChange"
        @current-change="currentChange"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="categories.total"
      >
      </el-pagination>
    </el-card>
    <el-dialog
      :title="form.cat_id ? '修改分类' : '添加分类'"
      :visible="dialogFormVisible"
    >
      <el-form :model="form" :rules="rules" ref="ruleForm">
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="form.cat_name"></el-input>
        </el-form-item>
        <el-form-item v-if="!form.cat_id" label="父级分类">
          <el-cascader
            v-model="value"
            :options="categories.result"
            :props="{
              checkStrictly: true,
              expandTrigger: 'hover',
              label: 'cat_name',
              value: 'cat_id',
            }"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="clearForm">取 消</el-button>
        <el-button type="primary" @click="sureCat">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      value: [],
      query: {
        pagesize: 5,
        pagenum: 1,
        type: "",
      },
      all: "",
      dialogFormVisible: false,
      form: {
        cat_name: "",
      },
      rules: {
        cat_name: [
          { required: true, message: "请输入分类名称", trigger: "blur" },
        ],
      },
    };
  },
  computed: {
    ...mapState({
      categories: (state) => state.categories.categories,
    }),
  },
  methods: {
    ...mapActions(["getCategories", "delCatItem", "addCatItem", "editCatItem"]),
    sizeChange(val) {
      this.query.pagesize = val;
      this.getCategories(this.query);
    },
    currentChange(val) {
      this.query.pagenum = val;
      this.getCategories(this.query);
    },
    // 获取所有分类
    // async allCat(){
    //   const res = await this.getCategories({query:'',pagesize:'',pagenum:''})
    //   console.log(res);
    //   this.all = res
    //   console.log(this.all);
    // },
    clearForm() {
      this.dialogFormVisible = false;
      this.form = { cat_name: "" };
    },
    delCat(id) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.delCatItem(id).then(() => {
            this.$message({
              type: "success",
              message: "删除成功!",
            });
            this.getCategories(this.query);
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    editCatDialog(row) {
      this.dialogFormVisible = true;
      this.form = { ...row };
    },
    addCatDialog() {
      this.dialogFormVisible = true;
    },
    sureCat() {
      this.dialogFormVisible = false;
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          if (this.form.cat_id) {
            this.editCatItem({
              id: this.form.cat_id,
              cat_name: this.form.cat_name,
            }).then(() => {
              this.$message.success("修改成功");
            });
          } else {
            console.log(this.value);
            let cat_pid = 0;
            if (this.value.length === 0) {
              cat_pid = 0;
            } else {
              cat_pid = this.value[this.value.length - 1];
            }
            this.addCatItem({
              cat_name: this.form.cat_name,
              cat_level: this.value.length,
              cat_pid,
            }).then(() => {
              this.$message.success("添加成功"),
                (this.dialogFormVisible = false);
              this.form = { cat_name: "" };
            });
          }
        }
      });
    },
  },
  created() {
    this.getCategories(this.query);
  },
};
</script>

<style>
.el-icon-success {
  color: #39ce39;
}
</style>